.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  .animated-background {
    background: linear-gradient(-45deg, #1a1a1a, #2c3e50, #34495e, #1a1a1a);
    background-size: 400% 400%;
    animation: gradientAnimation 15s ease infinite;
  }

/* Tech Theme Patterns and Effects */
.tech-pattern {
  background-color: #000000;
  background-image: 
    radial-gradient(rgba(25, 118, 210, 0.05) 2px, transparent 2px),
    radial-gradient(rgba(106, 90, 205, 0.03) 2px, transparent 2px);
  background-size: 40px 40px;
  background-position: 0 0, 20px 20px;
  animation: grid-animation 60s linear infinite;
}

/* Grid animation for tech background */
@keyframes grid-animation {
  0% {
    background-position: 0 0, 20px 20px;
  }
  100% {
    background-position: 40px 40px, 60px 60px;
  }
}

.tech-pattern::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(90deg, rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.8) 50%, rgba(0, 0, 0, 0.95) 100%),
    linear-gradient(0deg, rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.8) 50%, rgba(0, 0, 0, 0.95) 100%);
  z-index: -1;
}

/* Enhanced interactive elements */
.interactive-hover {
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275),
              box-shadow 0.3s ease;
}

.interactive-hover:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px -5px rgba(59, 130, 246, 0.3);
}

/* Tech card glow effect on hover */
.group:hover .text-blue-400 {
  text-shadow: 0 0 8px rgba(59, 130, 246, 0.6);
  transition: text-shadow 0.3s ease;
}

/* Enhanced gradient text */
.bg-clip-text.text-transparent.bg-gradient-to-r {
  background-size: 200% auto;
  animation: text-shine 3s linear infinite;
}

@keyframes text-shine {
  to {
    background-position: 200% center;
  }
}

/* Digital circuit connection effect for links */
a, button {
  position: relative;
  transition: color 0.3s ease, transform 0.3s ease;
}

a:hover, button:hover {
  transform: translateX(3px);
}

a:before, button:before {
  content: '';
  position: absolute;
  left: -10px;
  top: 50%;
  height: 1px;
  width: 6px;
  background: rgba(59, 130, 246, 0.7);
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.3s ease;
}

a:hover:before, button:hover:before {
  transform: scaleX(1);
}

/* Tech badge pulse effect */
.inline-flex.items-center.px-3.py-1.rounded-full {
  position: relative;
  overflow: hidden;
}

.inline-flex.items-center.px-3.py-1.rounded-full:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(59, 130, 246, 0.3);
  border-radius: 50%;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.5s ease, opacity 0.5s ease;
}

.inline-flex.items-center.px-3.py-1.rounded-full:hover:after {
  transform: scale(3);
  opacity: 0;
  animation: badge-pulse 1s ease;
}

@keyframes badge-pulse {
  0% {
    transform: scale(0);
    opacity: 0.7;
  }
  100% {
    transform: scale(3);
    opacity: 0;
  }
}

/* Language switcher styles */
.language-switcher {
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.language-switcher:hover {
  border-color: rgba(59, 130, 246, 0.8);
  box-shadow: 0 0 12px rgba(59, 130, 246, 0.3);
}

.language-switcher::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg, 
    transparent, 
    rgba(59, 130, 246, 0.2), 
    transparent
  );
  transition: left 0.7s ease;
}

.language-switcher:hover::before {
  left: 100%;
}

.language-indicator {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.language-indicator::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(to right, transparent, rgba(59, 130, 246, 0.7), transparent);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.language-indicator:hover::after {
  transform: scaleX(1);
}

.active-language {
  color: #60a5fa;
  font-weight: 500;
}
